import React, { useLayoutEffect,useEffect,useState } from 'react'
import {
    Box,
    Text,
    Heading,
    VStack,
    FormControl,
    Input,
    Link,Image,
    Button,
    HStack,Stack,Icon,
    Center,Actionsheet,Modal,
    NativeBaseProvider,AspectRatio,useDisclose, Pressable, FlatList, ScrollView
  } from "native-base";
import FooterView from '../template/FooterView';
import { getUrlServer } from '../ConfigSite';
import { ShowCard } from '../Components/Cards/ShowCard';
import { Dimensions } from 'react-native';
import { SafeAreaView } from 'react-native';
import FooterAuthView from './FooterAuthView';
import axios from 'axios';

const screen = Dimensions.get('screen');

const ShowPubView = (props) => {
    const {id } = props.route.params;
    const [pub, setPub] = useState({});
    const [title, setTitle] = useState('Article');
    const options = {
        showComments : true
    }

    useLayoutEffect(() => {
        props.navigation.setOptions({
            headerTitle: title
        })
    })
    useEffect(()=> {
        axios.get(getUrlServer('getPost&id='+id)).then(
            response => {
                setPub(response.data)
                setTitle(response.data.title);

            }
            ).catch(error => console.log(error))
            
       /* fetch()
        .then(
          result => result.json()
        ).then(json => {
            setPub(json);
            setTitle(json.title);
        })
        .catch(err => console.log(err))*/
      
  }, [])
    console.log(id);
    return (
<ScrollView flex={1}>
        <Box flex={2} alignItems="center"  width={screen.width}>
     
   <ShowCard  {...options}   {...pub}  {...props} />
  
        </Box>
        <FooterAuthView {...props} />
     </ScrollView>
        
    )
}

export default ShowPubView;
