import { Center, FormControl, HStack,Input, useToast, VStack } from 'native-base'
import React, {useState, useEffect} from 'react'
import { FontAwesome } from '@expo/vector-icons'

const NewsletterView = () => {
    const [email, setEmail] = useState('')
    const [displayToast, setDisplayToast] = useState(false)
    const toast = useToast();
    
    /**
     * 
     * @param {NativeSyntheticEvent} e 
     */
    const send = e => {
        console.log(e);
        if (e !== undefined ) {
            setEmail(e.target.value)
        }
        setDisplayToast(true)
        console.log(email);
        toast.closeAll()

    }
    useEffect(() => {
        if (displayToast){
            toast.show({
                title : "email send  correctly !",
                placement: 'top',
                color: 'green'
            })
        }
       
    }, [displayToast])
    return (
        <HStack justifyContent="space-around" space={1}>    
            <FormControl>
                <FormControl.Label _text={{
                    fontWeight: 'bold'
                }}>Répondez à la Newsletter</FormControl.Label>
                <HStack space={1}>
                <Input placeholderTextColor="white" placeholder="name@address.com" width="70%" size="sm" onSubmitEditing ={e => send(e)} onChangeText={email => setEmail(email)} />
                <FontAwesome name="send" onPress={() => send()} color="white" size={30} />
                </HStack>
            </FormControl>
        </HStack>
    )
}

export default NewsletterView
