import React , {useState, useEffect} from 'react'
import {
    Box,
    Text,
    Heading,
    VStack,
    FormControl,
    Input,
    Link,Image,
    Button,
    HStack,Stack,
    Center,Actionsheet,
    NativeBaseProvider,AspectRatio,useDisclose, Pressable, FlatList, Container
  } from "native-base";
import FontAwesome from '@expo/vector-icons/FontAwesome';
import AntDesign from '@expo/vector-icons/AntDesign';
import { Dimensions, TouchableOpacity } from 'react-native';
import { ShowCard } from '../Components/Cards/ShowCard';
import { getUrlServer } from '../ConfigSite';
import AsyncStorage from '@react-native-async-storage/async-storage';


const BlogView = props => {

  const [data, setData] = useState([])
  const [isLoading, setisLoading] = useState(true);
  const {userFirst, userId} = props;
  

  const options = {
    showComments : false
  }
  useEffect(()=> {
    if (userFirst) {
      fetch(getUrlServer('getPosts&userId='+userId))
      .then(
        result => result.json()
      ).then(json => {
        setData(json)
      })
      .catch(err => console.log(err));
    }else {
      fetch(getUrlServer('getPosts'))
      .then(
        result => result.json()
      ).then(json => {
        setData(json)
      })
      .catch(err => {
        AsyncStorage.getItem('articles').then(
          value => {
            if (value !== null){
              setData(JSON.parse(value));
              console.log('====================================');
    console.log('articles not null');
    console.log('====================================');
            }
          }
        )
        console.log(err)
      });
    }
        
      
  }, []);
  useEffect(()=> {
    (async ()=> {
      if (data.length > 1){
        console.log('====================================');
        console.log('okey');
        console.log('====================================');
        await AsyncStorage.setItem('articles', JSON.stringify(data));
      }
    })();
    

  }, [data]);
    return (
        <Box mt="2">
        <Container justifyContent="center" alignItems="center">
      <Heading  size="sm">{(userId )? "Mes articles" : "Nos articles"}</Heading>
      </Container>
<FlatList
  data={data}
  keyExtractor={item => item.id.toString()}
  renderItem={({item}) => <ShowCard {...options} {...item}  {...props} />}
 />
        </Box>
    )
}

export default BlogView
