import React, { useEffect , useState} from 'react'
import {
    Box,
    Text,
    Heading,
    VStack,
    FormControl,
    Input,
    Link,Image,
    Button,Spinner,ScrollView,
    HStack,Stack,Icon,
    Center,Actionsheet,Modal,
    NativeBaseProvider,AspectRatio,useDisclose, Pressable, FlatList, Divider
  } from "native-base";
import AsyncStorage from '@react-native-async-storage/async-storage';
import FontAwesome from '@expo/vector-icons/FontAwesome';
import AntDesign from '@expo/vector-icons/AntDesign';
import { Dimensions, Linking, Share, TouchableOpacity } from 'react-native';
import FooterView from '../template/FooterView';
//import { data } from '../../Model/data';
import { toShareInFB, tweetNow } from '../../Model/Functions';
import Config, { getUrlServer, getRootPicture } from '../ConfigSite';
import DateApp from '../../Model/DateApp';
import { ActivityIndicator } from 'react-native';
import { ShowCard } from '../Components/Cards/ShowCard';
import useAuth from '../Components/useAuth';
import FooterAuthView from './FooterAuthView';
import { useNavigation } from '@react-navigation/native';
//import { openDatabase } from 'react-native-sqlite-storage';
//var db = openDatabase({ name: 'UserDatabase.db' });
//import Realm from 'realm';

const screen = Dimensions.get('screen')
console.log(screen.width);
  
const BlogMemberView = props => {
  const [data, setData] = useState([]);
  const [auth, setAuth] = useState({});
  const navigation = useNavigation();
  const {authSession} = useAuth();
  const goToPost = () => {
    navigation.navigate('Post')
  }

  const [isLoading, setisLoading] = useState(true)
  const openCamera = () => {
    navigation.navigate('Camera')
    console.log('camera');
  }
  const options = {
    showComments : false
  }
  const goToCompte = () => {
    props.navigation.navigate('Compte', {
      user : auth
    })

  }
  const toFetch = async () => {
    fetch(getUrlServer('getPosts'))
        .then(
          result => result.json()
        ).then(json => {
          setData(json)
          setisLoading(false);
        })
        .catch(err => {
          AsyncStorage.getItem('articles').then(
            value => {
              if (value !== null){
                setData(JSON.parse(value));
                setisLoading(false);
                console.log('====================================');
      console.log('articles not null');
      console.log('====================================');
              }
            }
          )
          console.log(err, 'articles');
        });
  }

  console.log(authSession, 'session');
  useEffect(()=> {
    AsyncStorage.getItem('authSession').then(
      value => {
        if (value !== null){
          setAuth(JSON.parse(value))
        }
      }
    )
        toFetch();
        if (props?.route?.params?.refresh) {
          toFetch().then(
            async () => await AsyncStorage.setItem('articles', JSON.stringify(data))
          );
        }
      
  }, [navigation]);
  useEffect(()=> {
    (async ()=> {
      if (data.length > 1){
        console.log('====================================');
        console.log('okey');
        console.log('====================================');
        await AsyncStorage.setItem('articles', JSON.stringify(data));
      }
    })();
    

  }, [data]);
  
  console.log('render', auth, 'auth');
    return (
      <ScrollView flex={1}>
      <Box   width={screen.width}>
      <HStack m={2} p={2}  space={3} justifyContent="space-around">
      <Pressable onPress={goToCompte} w="10%">
      <Image alt="rok"  source={require('../../assets/image/IMG-20211106-WA0008.jpg')} rounded="md" height={30} width="100%" />
      </Pressable>

<FormControl width="50%">
    <Input  alignItems="center" placeholderTextColor="black" placeholder="Post an article" borderColor="indigo.400"  onFocus={() => goToPost()} rounded="md" />
</FormControl>
<Icon color="indigo.400" size={30} onPress= {() => openCamera()} as={<FontAwesome  name="file-picture-o"  />}  />
</HStack>
 {isLoading  && <Spinner size="lg" />}
<FlatList 
  data={data}
  keyExtractor={item => item.id.toString()}
  renderItem={({item}) => <ShowCard {...options} {...item}  {...props} />}
 />
      </Box>
      <FooterAuthView {...props} />
      </ScrollView>
    )
}

export default BlogMemberView
