import * as React from 'react';
import { NavigationContainer, useNavigation } from '@react-navigation/native';
import {
  createDrawerNavigator,
  DrawerContentScrollView,
} from '@react-navigation/drawer';
import { MaterialCommunityIcons, Entypo,Fontisto, FontAwesome5, MaterialIcons, AntDesign } from '@expo/vector-icons';
import {
  NativeBaseProvider,
  Button,
  Box,
  HamburgerIcon,
  Pressable,
  Heading,
  VStack,
  Text,
  Center,
  HStack,
  Divider,Image,
  Icon
} from 'native-base';
import { LoginView } from '../LoginView';
import { SignView } from '../SignView';
import BlogMemberView from '../members/BlogMemberView';
import PostView from '../members/PostView';
import HomeView from '../HomeView';
import FooterView from '../template/FooterView';
import AproposView from '../waoh/AproposView';
import LogOutView from '../LogOutView';
import ContactView from '../waoh/ContactView';
import CameraView, { SaveImage } from '../device/CameraView';
import DiscutionView from '../members/DiscutionView';
import FormationView from '../FormationView';
import ShowPubView from '../members/ShowPubView';
import CompteView from '../members/CompteView';
import ChatView from '../members/ChatView';
import PostFileView from '../members/PostFileView';
import { getIcon, GetIconBar, Title } from '../../Model/Functions';
import FooterAuthView from '../members/FooterAuthView';
import { AuthProvider } from './useAuth';
import MembersView from '../members/MembersView';
import BlogUserView from '../members/BlogUserView';


export const SetPressable = ({index, props, page, title,GetIcon}) => {
    
    const go = () => {
      props.navigation.navigate(page);
    }
  
      return (
  <Pressable
                  px="5"
                  py="3"
                  rounded="md"
                  bg='transparent'
                  _focus={{
                      backgroundColor : 'rgba(6, 182, 212, 0.1)'
                  }}
                  onPress={go}>
                  <HStack space="7" alignItems="center">
                  <Icon
                      color= 'gray.500'
                      size="5"
                      as={GetIcon}
                      onPress={go}
                    />
                    <Text
                      fontWeight="500"
                      color='gray.700'
                      >
                      {title}
                    </Text>
                  </HStack>
                </Pressable>
      )
      /**
       * 
       */
  }
  
  export function CustomDrawerContentAuth(props) {
  
    
      return (
        <DrawerContentScrollView {...props} safeArea>
          <VStack space="6" my="2" mx="1">
            <GetIconBar {...props} />
            <VStack divider={<Divider />} space="4">
              <VStack space="3">
              <SetPressable index={1} props={props} page="Home" title="Accueil" GetIcon={<Entypo name="home" />} />
              <SetPressable index={2} props={props} page="Contact" title="Nous contacter" GetIcon={<AntDesign name="contacts" />} />
              <SetPressable index={5} props={props} page="Formation" title="Nos formations" GetIcon={<MaterialCommunityIcons name="information" />} />
              <SetPressable index={6} props={props} page="Post" title="Speak" GetIcon={<MaterialCommunityIcons name="post" />} />
              <SetPressable index={7} props={props} page="Apropos" title="À propos" GetIcon={<AntDesign name="tago" />} />
              <SetPressable index={8} props={props} page="BlogMember" title="Espace membre" GetIcon={<MaterialIcons name="public" />} />
              <SetPressable index={9} props={props} page="Discution" title="Chats" GetIcon={<AntDesign name="wechat" />} />
              <SetPressable index={9} props={props} page="Members" title="Les membres" GetIcon={<Fontisto name="persons" />} />
              <SetPressable index={10} props={props} page="LogOut" title="Deconnection" GetIcon={<MaterialCommunityIcons name="toggle-switch-off" />} />
              </VStack>
            </VStack>
          </VStack>
        </DrawerContentScrollView>
      );
  }
  const Drawer = createDrawerNavigator();


export const  DrawerAuth = ( )=> {

  return (
    <Box safeArea flex={1}>
      <Drawer.Navigator
        drawerContent={(props) => <CustomDrawerContentAuth {...props} />}
        initialRouteName="BlogMember"
        screenOptions={{
          
          headerBackgroundContainerStyle : {
            backgroundColor : "blue"
          }
        }}
        
        >
        <Drawer.Screen name="BlogMember" component={BlogMemberView} options={{
            headerTitle : 'Espace membre'
            
        }} />
        <Drawer.Screen name="Post" component={PostView} />
        <Drawer.Screen name="Home" component={HomeView} options={{
            headerTitle : () => (
              <Title title="Accueil" />
            )
        }} />
        <Drawer.Screen name="FooterAuth" component={FooterAuthView} />
        <Drawer.Screen name="Members" component={MembersView} />
        <Drawer.Screen name="Apropos" component={AproposView} options={{
            headerTitle : () => (
              <Title title="À propos" />
            )
        }} />
        <Drawer.Screen name="LogOut" component={LogOutView} options={{
            headerShown : false
        }} />
        <Drawer.Screen name="Contact" component={ContactView} options={{
            headerTitle : () => (
              <Title title="Contact" />
            )
        }}  />
        <Drawer.Screen name="Camera" component={CameraView} options={{
            headerTitle : () => (
              <Title title="Gallerie" />
            )
        }} />
        <Drawer.Screen name="Discution" component={DiscutionView} />
        <Drawer.Screen name="BlogUser" component={BlogUserView} />
        <Drawer.Screen name="Formation" component={FormationView} />
        <Drawer.Screen name="ShowPub" component={ShowPubView} />
        <Drawer.Screen name="Compte" component={CompteView} />
        <Drawer.Screen name="Chat" component={ChatView} />
        <Drawer.Screen name="SaveImage" component={SaveImage} />
        <Drawer.Screen name="PostFile" component={PostFileView} />
      </Drawer.Navigator>
    </Box>
  );
}

export default function MyDrawerAuth(props) {
    const {authSession} = props.route.params;

    return (
        <AuthProvider authSession={authSession}>
          <DrawerAuth />
        </AuthProvider>
    );
}
