import React, { useEffect, useLayoutEffect, useState } from 'react'
import { StyleSheet,View, Text, TouchableOpacity,Platform, SafeAreaView, StatusBar, KeyboardAvoidingView, ScrollView, TouchableWithoutFeedback, Keyboard } from 'react-native'
import {Ionicons} from '@expo/vector-icons';
import {Icon, Input} from 'native-base';
// import { Avatar } from 'react-native-elements';
import {Avatar }from 'native-base'
import useAuth from '../Components/useAuth';
import axios from 'axios';
import { getUrlServer } from '../ConfigSite';
import AsyncStorage from '@react-native-async-storage/async-storage';


const ChatView = props => {
    const [input, setInput] = useState('');
    const {authSession} = useAuth();
    const [chat, setChat] = useState([]);
    const [auth, setAuth] = useState(authSession);
    
    const {otherId} = props.route.params;
    useLayoutEffect(() => {
        props.navigation.setOptions({
            headerRight : () => (
                <TouchableOpacity>
                    <Icon size={30} color="indigo.400" as={<Ionicons name="call"  />} />
                </TouchableOpacity>
            )
        })
    })
    useEffect(() =>  {
        AsyncStorage.getItem('authSession').then(
            value => {
              if (value !== null){
                setAuth(JSON.parse(value))
              }
            }
          )

        axios.get(getUrlServer(`getChat&receiveId=${otherId}&sendId=${auth.id}`)).then(data =>
            setChat(data.data)
        ).catch(err => console.log(err, 'error from axios get'))
    },[input]);
    
    const sendMessage = () => {
        Keyboard.dismiss();
        const data = new FormData();
        data.append('content', input);
        data.append('sendId', auth.id);
        data.append('receiveId', otherId);

        axios.post(getUrlServer('postChat'), data).then( 
            () => setInput('') 
        );
    }

    return (
        <SafeAreaView style={{flex: 1}}>
        <StatusBar style="light" />
            <KeyboardAvoidingView style={{
                flex:1
            }} behavior={Platform.OS === 'android' ? 'padding' : 'height'} keyboardVerticalOffset={90}>
                <TouchableWithoutFeedback onPress={Keyboard.dismiss}>
                <>
                    <ScrollView contentContainerStyle={{
                        paddingTop: 15

                    }}>
                    {chat.map(result => (
                        result.sendId === auth.id ?

<View key={result.id} style={styles.reciever} >
    <Avatar position="absolute" bottom={-15} right={-5}  size={30} rounded source={require('../../assets/image/IMG-20211106-WA0014.jpg')} />
    <Text style={styles.recieverText} >
        {result.content}
    </Text>
</View> :
<View key={result.id} style={styles.sender}>
<Avatar position="absolute" bottom={-15} right={-5}  size={30} rounded source={require('../../assets/image/IMG-20211106-WA0019.jpg')} />
        <Text style={styles.senderText} >
        {result.content}
        </Text>
        <Text style={styles.senderName} >Rostand</Text>
</View>
                    ))
                        
                    }
                    </ScrollView>
                    <View style={{
                            flexDirection : "row",
                            alignItems: "center",
                            width : "100%",
                            padding : 15
                    }}>
                        <Input onSubmitEditing={sendMessage} style={{
                            bottom: 0,
                            height: 40,
                            flex: 1,
                            marginRight : 15,
                            backgroundColor: "#ECECEC",
                            padding: 10,
                            color : 'grey',
                            borderRadius : 30
                        }} onChangeText={text => setInput(text)} value={input} placeholder="Message" />
                    <TouchableOpacity onPress={sendMessage} activeOpacity={0.5}>
                        <Ionicons name="send" size={24} color="#2B68E6" />
                    </TouchableOpacity>
                    </View>
                </>
                </TouchableWithoutFeedback>
                
            </KeyboardAvoidingView>
        </SafeAreaView>
    )
}

const styles = StyleSheet.create({
    avatar : {

    },
    reciever: {
        padding: 15,
        backgroundColor : "#ECECEC",
        alignSelf : "flex-end",
        borderRadius : 20,
        marginRight :15,
        marginBottom : 20,
        maxWidth : "80%",
        position : "relative"
    },
    recieverText : {
        color : 'black',
        fontWeight : "500",
        marginLeft : 10
    },
    sender : {
        padding : 15,
        backgroundColor : "#2B68E6",
        alignSelf : "flex-start",
        borderRadius: 20,
        margin : 15,
        maxWidth : "80%",
        position : "relative"
    },
    senderText: {
        color : 'white',
        fontWeight : "500",
        marginLeft : 10,
        marginBottom : 15
    },
    senderName : {
        left : 10,
        paddingRight : 10,
        fontSize : 10,
        color: 'white'
    }
})

export default ChatView;
