export const followToFB = () => {
  Linking.openURL(Config.pageFB)
  .then(data => console.log(data))
  .catch(err => console.log(err));
}

export const toShareInFB = (content) => {
  let facebookParameters = [];
if (Config.site)
  facebookParameters.push('u=' + encodeURI(Config.site));
if (content)
  facebookParameters.push('quote=' + encodeURI(content));
const url = 'https://www.facebook.com/sharer/sharer.php?' +
  facebookParameters.join('&');

Linking.openURL(url)
  .then((data) => {
    console.log('Facebook Opened  ', data);
  })
  .catch(err => {
    console.log('Something went wrong', err);
  });
}
export const tweetNow = (content) => {
  let twitterParameters = [];
  if (Config.site)
    twitterParameters.push('url=' + encodeURI(Config.site));
  if (content)
    twitterParameters.push('text=' + encodeURI(content));
  if (Config.twitterViaAccount)
    twitterParameters.push('via=' + encodeURI(Config.twitterViaAccount));
  const url =
    'https://twitter.com/intent/tweet?'
    + twitterParameters.join('&');
  Linking.openURL(url)
    .then((data) => {
      console.log();('Twitter Opened', data);
    })
    .catch(err => {
      console.log();('Something went wrong', err);
    });
};
