import React, { useState, useEffect } from 'react';
import { StyleSheet, View, TouchableOpacity, Dimensions, Button, TextInput ,} from 'react-native';
import { Camera } from 'expo-camera';
import {alignSelf} from 'styled-system';
import { Image } from 'react-native';
import * as ImagePicker from 'expo-image-picker';
import { useNavigation } from '@react-navigation/native';
import {Center, Text} from 'native-base';



export const SaveImage = props => {

    const {image} = props.route.params;

    console.log(image);
    return (
        <View style={{
            flex: 1
        }}>
    <Image source={{uri : image}} />
    <TextInput  
        placeholder="Write a Caption "
        
    />
        </View>
    )
}

const screen = Dimensions.get('screen');
export default function CameraView() {
    
    const navigation = useNavigation()
    const [hasGalleryPermission, setHasGalleryPermission] = useState(null);
    const [hasCameraPermission, setHasCameraPermission] = useState(null);

    const [camera, setCamera] = useState(null);
    const [image, setImage] = useState(null);
    const [imageObject, setImageObject] = useState({});


    const [type, setType] = useState(Camera.Constants.Type.back);
    useEffect(() => {
        (async () => {
            const cameraStatus = await Camera.requestCameraPermissionsAsync()
            setHasCameraPermission(cameraStatus.status === 'granted');
            const galleryStatus = await ImagePicker.requestCameraPermissionsAsync();
            setHasGalleryPermission(galleryStatus.status === 'granted');
            
            
            /*if (galleryStatus.status !== 'granted') {
                alert("sorry, we camera roll permissions to make this picture")
            }*/

        })();
    }, []);
    if (hasCameraPermission === null || hasGalleryPermission === false) {
        return <View />;
    }
    
    if (hasCameraPermission === false || hasGalleryPermission === false) {
        return <Center flex={1} >
            <Text>No access to camera</Text>
        </Center>;
    }
    const takePicture = async () => {
        if (camera){
            const data = await camera.takePictureAsync(null);
            console.log(data.uri);
            setImage(data.uri);
        }
    }
    const pickImage = async () => {
        let result = await ImagePicker.launchImageLibraryAsync({
            mediaTypes : ImagePicker.MediaTypeOptions.Images,
            allowsEditing : true,
            aspect : [1, 1],
            quantity : 1
        });
        console.log(result);

        if (!result.cancelled){
            setImage(result.uri);
            setImageObject(result);

        }
    }
    return (
        <View style={{
flex : 1
        }} >
        <View style={styles.cameraContainer}>
        <Camera 
        ref={ref => setCamera(ref)}
        style={styles.fixedRatio} 
        type={type}
        ratio={'1:1'}

        />

        </View>

          
                    <Button
                    
                    title="Flip Image"
                        onPress={() => {
                            setType(
                                type === Camera.Constants.Type.back
                                    ? Camera.Constants.Type.front
                                    : Camera.Constants.Type.back
                            );
                        }}
                         />
        <Button title="Take Picture" onPress={
            () => takePicture()
        } />
         <Button title="Pick Image from Gallery" onPress={
            () => pickImage()
        } /> 
         <Button title="Save" onPress={
            () => navigation.navigate('Post', {
                image, imageObject
            })
        } />
        { image && <Image source={
        {uri : image}} style={{
            flex : 1
        }} /> 
        }
        </View>

    );
}
const styles = StyleSheet.create({
    cameraContainer : {
        flex: 1,
        flexDirection: 'row'

    },
    fixedRatio : {
        flex : 1,
        aspectRatio : 1
    }
})