import React, { useLayoutEffect, useState, useEffect } from 'react'
import { ScrollView, TouchableOpacity} from 'react-native'
import { View, Text } from 'react-native';
import { ListItem,Avatar } from 'react-native-elements';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { Radio } from 'native-base';
import axios from 'axios';
import { getUrlServer } from '../ConfigSite';

//2h 14 
const MembersView = (props) => {

  const [auth, setAuth] = useState({});
    const [users, setUsers] = useState([]);

    const goToCompte = user => {
        props.navigation.navigate('Compte', {
            user
        });
    }
    useLayoutEffect(
        () => {
            props.navigation.setOptions({
                headerRight : () => (
                    <View style={{marginRight : 20}}>
<TouchableOpacity activeOpacity={0.5} onPress={goToCompte}>
                    <Avatar rounded source={require('../../assets/image/IMG-20211106-WA0017.jpg')} />
                    </TouchableOpacity>
                    </View>
                    
                )
            })
        }, []);
        useEffect(() => {
            AsyncStorage.getItem('authSession').then(response => {
              if (response !== null) {
                setAuth(JSON.parse(response));
              }
            })
            axios.get(getUrlServer('getUser')).then(
                data => setUsers(data.data)
            ).catch(err => console.log(err));
            
        },[]);
    return (
        <ScrollView style={{flex : 1}}>
        {users.map(user => (
            <ListItem onPress={() => goToCompte(user)} key={user.id} bottomDivider>
           <Avatar rounded
           source={require('../../assets/image/IMG-20211106-WA0010.jpg')}
            />
            <ListItem.Content>
                <ListItem.Title style={{fontWeight : '800'}}>
                        {user.first_name}
                </ListItem.Title>
                <ListItem.Subtitle numberOfLines={1} ellipsizeMode="tail" >
                    Conseiller
                </ListItem.Subtitle>
            </ListItem.Content>
            <Radio.Group
            accessibilityLabel="aria-label"
            >
            <Radio  accessibilityLabel="aria-label" colorScheme="green" size="md" my={1} />
            </Radio.Group>
       </ListItem>
        ))}
        </ScrollView>
       
    )
}

export default MembersView;
