import React, {useState} from "react"
import {
  Box,
  Heading,
  VStack,
  FormControl,
  Input,
  Button,
  Center,
  NativeBaseProvider,
  View,
  ScrollView,
  Select,
  CheckIcon,
  WarningOutlineIcon
} from "native-base"
import FooterView from "./template/FooterView";
import AsyncStorage from '@react-native-async-storage/async-storage';

import Config, { getUrlServer, getRootPicture } from './ConfigSite';
import axios from "axios";

export const SignView = props => {
    const [password, setPassword] = useState('');
    const [confirm, setConfirm] = useState('');
    const [lastName, setLastName] = useState('');
    const [genre, setGenre] = useState('H');

  const [isValid, setisValid] = useState(false);
    const [username, setUsername] = useState('');
    const [phone, setPhone] = useState('');
    const setInput = e => {
        console.log(e, password, username)
    }
    const postForm = () => {
        console.log(password, username, confirm)
        if ((password !== confirm) || (lastName.length < 1 && username.length < 1 && phone.length < 1)) {
          
            setisValid(true);
          
        }else{
          const data = new FormData();
            data.append('password', password);
            data.append('first_name', username);
            data.append('last_name', lastName);
            data.append('phone', phone);
            data.append('genre', genre);

            axios.post(getUrlServer('postUser'), data).then(
              async response => {
                if (response.status === 200) {
                  console.log(response.data, 'data=====');
                  await AsyncStorage.setItem('authSession', JSON.stringify({
                    first_name : username,
                    password : password,
                    last_name : lastName,
                    phone : phone,
                    genre : genre
                  }))
                    //props.navigation.navigate('BlogMember')
    
                }else {
                    console.log('Post fail');
                }
              }
            );
            
            
        }
        
    }
  return (
    <ScrollView flex={1}> 
        <Box safeArea p="2" w="100%" py="8">
      <Heading
        size="lg"
        color="coolGray.800"
        _dark={{
          color: "warmGray.50",
        }}
        fontWeight="semibold"
      >
        Welcome
      </Heading>
      <Heading
        mt="1"
        color="coolGray.600"
        _dark={{
          color: "warmGray.200",
        }}
        fontWeight="medium"
        size="xs"
      >
        Sign up to continue!
      </Heading>
      <VStack space={3} mb="5" mt="5">
        <FormControl isValid={isValid}>
          <FormControl.Label>Email or Username</FormControl.Label>
          <Input autoFocus value={username} onChangeText={pass => setUsername(pass)} />
          <FormControl.ErrorMessage>Complete your email or username</FormControl.ErrorMessage>
        </FormControl>
        <FormControl isValid={isValid}>
          <FormControl.Label>Last name</FormControl.Label>
          <Input value={lastName} onChangeText={pass => setLastName(pass)} />
          <FormControl.ErrorMessage>Complete your last name</FormControl.ErrorMessage>
        </FormControl>
        <FormControl isRequired={isValid} isInvalid={isValid}>
        <FormControl.Label>Choose</FormControl.Label>
        <Select
          minWidth="200"
          accessibilityLabel="Choose Service"
          placeholder="Choose Service"
          _selectedItem={{
            bg: "teal.600",
            endIcon: <CheckIcon size={5} />,
          }}
          mt="1"
          onValueChange={(itemValue) => setGenre(itemValue)}
        >
          <Select.Item label="Homme" value="H" />
          <Select.Item label="Femme" value="F" />
        </Select>
        <FormControl.ErrorMessage leftIcon={<WarningOutlineIcon size="xs" />}>
          Please make a selection!
        </FormControl.ErrorMessage>
        </FormControl>
        <FormControl isValid={isValid}>
          <FormControl.Label>Phone</FormControl.Label>
          <Input type="number" value={phone} onChangeText={pass => setPhone(pass)} />
          <FormControl.ErrorMessage>Complete your cell phone number</FormControl.ErrorMessage>
        </FormControl>
        <FormControl isValid={isValid}>
          <FormControl.Label>Password</FormControl.Label>
          <Input secureTextEntry value={password} onChangeText={pass => setPassword(pass)} type="password" />
          <FormControl.ErrorMessage>Complete your password</FormControl.ErrorMessage>
        </FormControl>
        <FormControl isValid={isValid}>
          <FormControl.Label>Confirm Password</FormControl.Label>
          <Input onSubmitEditing={postForm} value={confirm} onChangeText={pass => setConfirm(pass)} type="password" />
          <FormControl.ErrorMessage>Confirm your password</FormControl.ErrorMessage>
        </FormControl>
        <Button onPress={postForm} mt="2" colorScheme="indigo">
          Sign up
        </Button>
      </VStack>
    </Box>
    <FooterView {...props} />
    </ScrollView>

  )
}