import React, {useState,useEffect, useLayoutEffect} from "react"
import {
  Box,
  Text,View,
  Heading,
  VStack,
  FormControl,
  Input,
  Link,
  Button,
  HStack,
  Center,Icon,
  NativeBaseProvider
} from "native-base";

import FooterView from "./template/FooterView";
import * as Facebook from 'expo-facebook';
import Config, { getUrlServer } from "./ConfigSite";
import {Entypo} from '@expo/vector-icons';
import useAuth from './Components/useAuth';
import { useNavigation } from "@react-navigation/native";
import AsyncStorage from '@react-native-async-storage/async-storage';
import { Platform,Alert } from "react-native";


export const LoginView = props => {
  const [password, setPassword] = useState('');

  const [userInfo, setUserInfo] = useState({})
  const [username, setUsername] = useState('');
  const [isValide, setIsValide] = useState(false);
  const navigation = useNavigation();

    const goToLogin = () => {

        navigation.navigate('Sign');
    }
    /*useLayoutEffect(() => {
        props.navigation.setOptions({
            headerTitle: "Login"
        })
    })*/
    const postForm = () => {
      console.log(password, username)
      if (password.length < 1 ||  username.length < 1){
        setIsValide(true)
      }else {

        if (!isValide) {
          const data = new FormData();
          data.append('first_name' , username);
          data.append('password' , password);
  
          fetch(getUrlServer('login'), {
            method: 'post',
            body : data
          }).then(result =>
              result.json()
            ).then(async user => 
              
              {
                await AsyncStorage.setItem('authSession', JSON.stringify(user));
                
                props.navigation.navigate('MyDrawerAuth', 
                {
                  authSession : user
                });
              })
            .catch(err => {
              console.log(err);
              /*props.navigation.navigate('MyDrawerAuth',
              {
                authSession : {}
              }
              );*/
              setIsValide(true);
          });
          setLoading(true)
        }
        
        
        
      }
      
      
  }
  async function logIn() {
    if (Platform.OS !== 'web') {
      try {
        await Facebook.initializeAsync({
          appId: Config.appFbId,
        });
        const { type, token, expirationDate, permissions, declinedPermissions } =
          await Facebook.logInWithReadPermissionsAsync({
            permissions: ['public_profile'],
          });
        if (type === 'success') {
          // Get the user's name using Facebook's Graph API
          const response = await fetch(`https://graph.facebook.com/me?access_token=${token}`);
          Alert.alert('Logged in!', `Hi ${(await response.json()).name}!`, console.log(await response.json()));
        } else {
          // type === 'cancel'
        }
      } catch ({ message }) {
        alert(`Facebook Login Error: ${message}`);
      }
    } else {
      alert("Web FB login");
    }
    
  }
  const toLoginFB = () => {
    logIn().then(
      console.log('trop cool')
    ).catch(err => console.error(err))
  }
  const [loading, setLoading] = useState(false)

  useEffect(() => {
    console.log(loading);
      AsyncStorage.getItem('authSession').then(
        value => {
          if (value !== null){
            let auth = JSON.parse(value);
            setUsername(auth.first_name);
            setPassword(auth.password);
            console.log(value, 'authSession');
          }
        }
      )
    return () => {
      setLoading(false);
      setIsValide(false)
    }
  }, [loading]);

  useLayoutEffect(()=> {
    navigation.setOptions({
      headerShown : false
    })
  }, [navigation])
  return (
    <Box flex={1} safeArea p="2" py="8" w="100%">
      <Heading
        size="lg"
        fontWeight="600"
        color="coolGray.800"
        _dark={{
          color: "warmGray.50",
        }}
      >
        Welcome
      </Heading>
      <Heading
        mt="1"
        _dark={{
          color: "warmGray.200",
        }}
        color="coolGray.600"
        fontWeight="medium"
        size="xs"
      >
        Sign in to continue!
      </Heading>

      <VStack space={3} mt="5">
        <FormControl isInvalid={isValide}>
          <FormControl.Label>Email ID</FormControl.Label>
          <Input autoFocus value={username} onChangeText={ text => setUsername(text)} />
      <FormControl.ErrorMessage>Complete your email</FormControl.ErrorMessage>

        </FormControl>
        <FormControl isInvalid={isValide}>
          <FormControl.Label>Password</FormControl.Label>
          <Input onSubmitEditing={postForm} secureTextEntry value={password} onChangeText={ pass => setPassword(pass)}  type="password" />
          <FormControl.ErrorMessage>Complete your password</FormControl.ErrorMessage>
          <Link
            _text={{
              fontSize: "xs",
              fontWeight: "500",
              color: "indigo.500",
            }}
            alignSelf="flex-end"
            mt="1"
          >
            Forget Password?
          </Link>
        </FormControl>
        <Button isLoading={loading} onPress={() => postForm()} mt="2" colorScheme="indigo">
          Sign in
        </Button>
        <Button  onPress={() => toLoginFB()} mt="2" colorScheme="indigo">
        <HStack  justifyContent="space-between" space={2}>
        <Text color="white">Continue with </Text>
          <Icon size={30} color="white" as={<Entypo name="facebook" />} />
        </HStack>
        </Button>
       
        <HStack mt="6" justifyContent="center">
          <Text
            fontSize="sm"
            color="coolGray.600"
            _dark={{
              color: "warmGray.200",
            }}
          >
            I'm a new user.{" "}
          </Text>
          <Link
            _text={{
              color: "indigo.500",
              fontWeight: "medium",
              fontSize: "sm",
            }}
            href="#"
            onPress={
                () => goToLogin()
            }
          >
            Sign Up
          </Link>
        </HStack>
      </VStack>
      <FooterView {...props} />
    </Box>
  )
}