import React , {useState, useEffect} from 'react'
import {
    Box,
    Text,
    Heading,
    VStack,
    FormControl,
    Input,
    Link,Image,
    Button,
    HStack,Stack,
    Center,Actionsheet,
    NativeBaseProvider,AspectRatio,useDisclose, Pressable, FlatList, Container
  } from "native-base";
import FontAwesome from '@expo/vector-icons/FontAwesome';
import AntDesign from '@expo/vector-icons/AntDesign';
import { Dimensions, TouchableOpacity } from 'react-native';
import { ShowCard } from '../Components/Cards/ShowCard';
import { getUrlServer } from '../ConfigSite';
import AsyncStorage from '@react-native-async-storage/async-storage';
import axios from 'axios';


const BlogUserView = props => {

  const [data, setData] = useState([])
  const [isLoading, setisLoading] = useState(true);
  const { userId } = props;
  

  const options = {
    showComments : false
  }
  useEffect(()=> {
      axios(getUrlServer('getPosts&userId='+userId))
      .then(
        response => setData(response.data)
      )
      .catch(err => {
        AsyncStorage.getItem('articles'+userId).then(
          value => {
            if (value !== null){
              setData(JSON.parse(value));
              console.log('====================================');
    console.log('articles not null user');
    console.log('====================================');
            }
          }
        )
        console.log(err)
      });
    
        
      
  }, []);
  useEffect(()=> {
    (async ()=> {
      if (data.length > 1){
        console.log('====================================');
        console.log('okey userId');
        console.log('====================================');
        await AsyncStorage.setItem('articles'+userId, JSON.stringify(data));
      }
    })();
    

  }, [data]);

  console.log(userId, 'userId');
    return (
        <Box mt="2">
        <Container justifyContent="center" alignItems="center">
      <Heading  size="sm">Mes articles</Heading>
      </Container>
<FlatList
  data={data}
  keyExtractor={item => item.id.toString()}
  renderItem={({item}) => <ShowCard {...options} {...item}  {...props} />}
 />
        </Box>
    )
}

export default BlogUserView;
