import * as React from 'react';
import { NavigationContainer } from '@react-navigation/native';
import {
  createDrawerNavigator,
  DrawerContentScrollView,
} from '@react-navigation/drawer';
import { MaterialCommunityIcons, Entypo, FontAwesome5, MaterialIcons, AntDesign } from '@expo/vector-icons';
import {
  NativeBaseProvider,
  Button,
  Box,
  HamburgerIcon,
  Pressable,
  Heading,
  VStack,
  Text,
  Center,
  HStack,
  Divider,Image,
  Icon
} from 'native-base';
import { getIcon, GetIconBar, Title } from './Model/Functions';
import BlogMemberView from './Views/members/BlogMemberView';
import PostView from './Views/members/PostView';
import { LoginView } from './Views/LoginView';
import { SignView } from './Views/SignView';
import FooterView from './Views/template/FooterView';
import ContactView from './Views/waoh/ContactView';
import AproposView from './Views/waoh/AproposView';
import HomeView from './Views/HomeView';
import CameraView from './Views/device/CameraView';
import ShowPubView from './Views/members/ShowPubView';
import CompteView from './Views/members/CompteView';
import ChatView from './Views/members/ChatView';
import PostFileView from './Views/members/PostFileView';
import DiscutionView from './Views/members/DiscutionView';
import FormationView from './Views/FormationView';
import MyDrawerAuth from './Views/Components/DrawerAuth';
// 1h 12 
const Drawer = createDrawerNavigator();

const SetPressable = ({index, props, page, title,GetIcon}) => {
    
  const go = () => {
    props.navigation.navigate(page);
  }

    return (
<Pressable
                px="5"
                py="3"
                rounded="md"
                bg='transparent'
                _focus={{
                    backgroundColor : 'rgba(6, 182, 212, 0.1)'
                }}
                onPress={go}>
                <HStack space="7" alignItems="center">
                <Icon
                    color= 'gray.500'
                    size="5"
                    as={GetIcon}
                    onPress={go}
                  />
                  <Text
                    fontWeight="medium"
                    color='gray.700'
                    >
                    {title}
                  </Text>
                </HStack>
              </Pressable>
    )
    /**
     * 
     */
}

function CustomDrawerContent(props) {

  
    return (
      <DrawerContentScrollView {...props} safeArea>
        <VStack space="6" my="2" mx="1">
         
          <GetIconBar {...props} />
          <VStack divider={<Divider />} space="4">
            <VStack space="3">
            <SetPressable index={1} props={props} page="Home" title="Accueil" GetIcon={<Entypo name="home" />} />
            <SetPressable index={2} props={props} page="Login" title="Se connecter" GetIcon={<Entypo name="login" />} />
            <SetPressable index={3} props={props} page="Sign" title="S'inscrire" GetIcon={<FontAwesome5 name="sign" />} />
            <SetPressable index={4} props={props} page="Contact" title="Nous contacter" GetIcon={<AntDesign name="contacts" />} />
            <SetPressable index={5} props={props} page="Formation" title="Nos formations" GetIcon={<MaterialCommunityIcons name="information" />} />
            <SetPressable index={6} props={props} page="Apropos" title="À propos" GetIcon={<AntDesign name="tago" />} />
            </VStack>
          </VStack>
        </VStack>
      </DrawerContentScrollView>
    );
}
export function MyDrawer() {
  return (
    <Box safeArea flex={1}>
      <Drawer.Navigator
        drawerContent={(props) => <CustomDrawerContent {...props} />}
        initialRouteName="Home"
        screenOptions={{
          
          headerBackgroundContainerStyle : {
            backgroundColor : "blue"
          }
        }}
        >
        <Drawer.Screen name="Login" component={LoginView} options={{
            headerTitle : 'Se connecter'
            
        }}  />
        <Drawer.Screen name="Sign" component={SignView} options={{
          headerTitle : () => (
              <Title title="S'inscrire" />
            )
        }}  />
       
        <Drawer.Screen name="Home" component={HomeView} options={{
            headerTitle : () => (
              <Title title="Accueil" />
            )
        }} />
        <Drawer.Screen name="Footer" component={FooterView} />
        <Drawer.Screen name="Apropos" component={AproposView} options={{
            headerTitle : () => (
              <Title title="À propos" />
            )
        }} />
        <Drawer.Screen name="Contact" component={ContactView} options={{
            headerTitle : () => (
              <Title title="Contact" />
            )
        }}  />
        
        <Drawer.Screen name="Formation" component={FormationView} options={{
            headerTitle : () => (
              <Title title="Formation" />
            )
        }} />
        <Drawer.Screen name="MyDrawerAuth" component={MyDrawerAuth} options={{
            headerShown : false
        }} />
      </Drawer.Navigator>
    </Box>
  );
}

export default function App() {

  
  return (
    <NavigationContainer>
      <NativeBaseProvider>
        <MyDrawer />
      </NativeBaseProvider>
    </NavigationContainer>
  );
}
